function requestError(that, code, code2) {
	let error;
	switch (code) {
		case '401':
			error = that.i18n.login.loginOverdue //''
			break;
		case '402':
			switch (code2) {
				case 'userId':
					error = that.i18n.login.checkParam //
					break;
				case 'oldPassword':
					error = that.i18n.login.oldPasswordError //
					break;
				default:
					error = that.i18n.login.checkParam //
			}
			break;
		case '403':
			error = that.i18n.login.authorityDeficiency //
			break;
		case '501':
			error = that.i18n.login.againLater //
			break;
		case '502':
			error = settingError(that,code2)
			break;
		default:
			error = that.i18n.login.unknownError //
	}
	return {
		code,
		code2,
		error
	}
}


function settingError(that, code) {
	let error;
	switch (code) {
		case '1':
			error = that.i18n.deviceController.wrongPort //''
			break;
		case '2':
			error = that.i18n.deviceController.wrongCommand //''
			break;
		case '3':
			error = that.i18n.deviceController.wrongParameter //''
			break;
		case '4':
			error = that.i18n.setFail //''
			break;
		case '5':
			error = that.i18n.deviceController.hardwareNotSupport //''
			break;
		case '602':
			error = that.i18n.deviceController.operateTooFast //''
			break;
		case '603':
			error = that.i18n.deviceController.equipmentOffline //''
			break;
		case '604':
			error = that.i18n.deviceController.deviceResponseTimeout //''
			break;
		case '606':
			error = that.i18n.deviceController.serviceNotSupport //''
			break;
	}
	return error
}


export {
	requestError,
	settingError
}
